.wallet {
  background-color: #00000077;
  position: absolute;
  display: none;
}

/* wrappers */

* {
  background-size: contain;
  background-repeat: no-repeat;
  box-sizing: border-box;
}

.container {
  background-repeat: repeat;
  background-color: #412a85;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  overflow: auto;
  color: #000;
  font-family: 'Gothic A1', sans-serif;
}

.content {
  background-color: #fff;
  margin: 0;
  display: flex;
  flex-direction: column;
  border-bottom: 100px solid #412a85;
}

/* sections */

.hero {
  display: flex;
  color: #fff;
  padding-bottom: 50px;
  min-height: 50vh;

  background-position-x: right;
  background-size: cover;
}
.cta {
  padding-top: 83px;
  padding-left: 60px;
  width: 50%;
}
.cta .hl {
  font-size: 34px;
  font-weight: 800;
  text-shadow: 0 0 2px black;
}
.cta .cp {
  text-shadow: 0 0 2px black;
}
.mi {
  display: none;
}

.mint {
  margin: 10px 0px;
}
.nftinfo {
  text-shadow: 0 0 2px black;
}

.details{
  display: flex;
  /* flex: 1; */
  justify-content: center;
  text-align: center;
  padding: 20px;
}
.details .hl {
  font-size: 21px;
  font-weight: 700
}

.items {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 20px;
}
.item {
  text-align: center;
  padding: 20px;
}

.item .blk {
  width: 100px;
  height: 100px;
  min-width: 100px;
  background-color: #000;
  display: inline-block;
  margin-bottom: 20px;
  flex: 0;
}
.item .ctx {
  flex: 1;
}

.footer {
  height: 130px;
  background-position-x: center;
}

/* elems */

strong { 
  display: block;
}

p {
  color: #666;
}

.somelinks {
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.somelinks a div {
  border-radius: 100%;
  width: 60px;
  height: 60px;
  margin: 10px;
  background-size: cover;
  background-position: center center;
}

.blocklink {
  margin-top: 10px;
  display: block;
}

a:link, a:visited {
  color: #000;
  text-decoration: none;
  font-weight: 600;
}
a:hover {
  color: purple;
  text-decoration: underline;
  
}

@media only screen and (max-width: 600px) {
  .container {
    background-repeat: repeat;
  }
  .content {
    margin: 20px;
    margin-top: 0;
    border-bottom: none;
  }
  .hero {
    background-image: none !important;
  }
  .cta {
    padding-left: 20px;
    padding-right: 20px;
    width: 100%;
  }
  .mi {
    display: block;
    width: 100%;
    /* border: 1px solid #0f0; */
    height: calc(100vw * 0.6);
    margin-top: 100px;
    margin-bottom: -20px;
    background-size: inherit;
  }
  .items {
    flex-direction: column;
  }
  .item {
    text-align: left;
    display: flex;
    flex-direction: row;
  }
  .item .blk {
    margin-right: 20px;
    margin-bottom: 0px;
  }
  .footer {
    margin: 0 -20px;
  }
}